<template>
  <div class="projectsCard">
    <h3>{{ name }}</h3>
    <img
      :src="getImageURL(imageSrc)"
      :alt="name + ' Image'" />
    <p class="about">{{ about }}</p>
    <div class="code">
      <a
        :href="activeSite"
        target="_blank">qq.com</a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ProjectsCard',
  props: {
    name: {
      type: String,
      default: ''
    },
    imageSrc: {
      type: String,
      default: ''
    },
    about: {
      type: String,
      default: ''
    },
    gitHub: {
      type: String,
      default: ''
    },
    activeSite: {
      type: String,
      default: ''
    }
  },
  methods: {
    getImageURL(image) {
      if (image) {
        return require(`@/assets/images/${image}`);
      }
    }
  }
};
</script>

